import React, { useRef, useContext, useState } from 'react';
import { updateKW } from "../../utils/ruleActions";
import { FormContext } from "../FormProvider/FormProvider";
import convert from "../assets/img/convert.png"
import configData from "../../config.json";

const TextArea = () => {
    const { setFormContent, setError, setIsFocused, setFormFields, content, error, isFocused } = useContext(FormContext);
    const searchInput = useRef(null)
    const [convertFlag, setConvert] = useState(false)
    const [ruleContent, setRuleContent] = useState(null)

    const onchange = (event) => {
        if (isFocused) { 
            updateKW(event.target.value, setError, setFormFields) }
            setFormContent(event.target.value)
            setRuleContent(event.target.value)
    }
    const convert_rule = (rule)=>{
        const myBody = JSON.stringify({ "rule": ruleContent })
        const url = configData.baseUrl + "/convert2to3/";
        console.log(url)
        fetch(url, { body: myBody, method: "post", "Content-Type": "application/json", 'Accept': 'application/json' }).then((response) => {
            response.json().then(
                (data) => {
                    if (data.stderr){
                        setError(data.stderr)
                    }
                    else{
                    updateKW(data.data, setError, setFormFields)
                    setFormContent(data.data);
                    setRuleContent(data.data);
                    }
                }
            )
        }).catch((e) => { console.log(e) });
        setConvert(false)
    }
    if (convertFlag){
        return <>
            <textarea ref={searchInput} style={{margin:"10px", "display":"flex"}} type="textarea" name="content" cols="40" rows="10" className="vLargeTextField" maxLength="2048" required="" id="id_content"
            onChange={onchange}
            onFocus={() => setIsFocused(true)}
            onBlur={() => setIsFocused(false)}
            value={content} />
            <textarea onChange={(e)=>setRuleContent(e.target.value)} style={{margin:"10px"}} type="textarea" name="content_2" cols="40" rows="10" className="vLargeTextField" maxLength="2048" required="" id="id_content_2"
            value={ruleContent} placeholder="snort 2 rule"/>
    
            <img alt="clean" style={{ "margin":"20px", "margin-bottom":"40px","width": "150px" } } src={convert} onClick={()=>convert_rule(ruleContent)}></img>
            <label style={{ color: "red" , maxWidth: "fit-content"}}  title="error">{error}</label>
            </>
        }
    return <>
        <textarea ref={searchInput} style={{}} type="textarea" name="content" cols="40" rows="10" className="vLargeTextField" maxLength="2048" required="" id="id_content"
            onChange={onchange}
            onFocus={() => setIsFocused(true)}
            onBlur={() => setIsFocused(false)}
            value={content} />
        <img alt="clean" style={{ "margin":"20px", "margin-bottom":"40px","width": "150px" } } src={convert} onClick={()=>setConvert(true)}></img>
        <label style={{ color: "red" , maxWidth: "fit-content", "display":"flex"}}  title="error">{error}</label>
    </>
}

export default TextArea;